import React, { useState, useEffect } from "react";
import Layout from "./../components/Layout/Layout";
import { useCart } from "../context/cart";
import { useAuth } from "../context/auth";
import { NavLink, useNavigate, Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import axios from "axios";
import toast from "react-hot-toast";
import i18n from "i18next";
import emailjs from 'emailjs-com';
const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};


const CartPage = () => {
  const { t } = useTranslation();
  const currentPage = 'CartPage';
  const [auth, setAuth] = useAuth();
  const [cart, setCart] = useCart();
  const [clientToken, setClientToken] = useState("");
  const [instance, setInstance] = useState("");
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const [deliveryMethod, setDeliveryMethod] = useState("");
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true);
      }
    });
  }, []);
  // Calculate total price including selected sizes
  const totalPrice = () => {
    return cart.reduce((acc, item) => acc + ((item.selectedPrice || item.price) * item.quantity), 0).toLocaleString("fr-FR", {
      // style: "currency",
      // currency: "lei",
    });
  };

  // Remove item from cart
  const removeCartItem = (pid, size) => {
    const updatedCart = cart.filter((item) => item._id !== pid || item.selectedSize !== size);
    setCart(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart));
  };

  useEffect(() => {
    // Load cart from localStorage on component mount
    const savedCart = JSON.parse(localStorage.getItem("cart")) || [];
    setCart(savedCart);
  }, [setCart]);

  const handlePlaceOrder = () => {
    setLoading(true);
    sendEmail();
  };


  const sendEmail = () => {
    const userDetails = {
      name: auth.user.name,
      phone: auth.user.phone,
      address: auth.user.address
    };

    // Construim detaliile produselor folosind un newline ca separator
    const productDetails = cart.map(item =>
      `${item.quantity}x ${item.name_ro} (Mrime: ${item.selectedSize}, Pre: ${item.selectedPrice || item.price} lei)`
    ).join("\n");  // Utilizm \n pentru a separa produsele pe linii noi

    const emailParams = {
      from_name: "i-sleep",
      to_name: "Clientul",
      subject: "Detalii Comand - i-sleep",
      user_name: userDetails.name,
      user_phone: userDetails.phone,
      user_address: userDetails.address,
      products_details: productDetails, // Detalii produse separate pe linii noi
      total_price: `${totalPrice()}`,
      delivery_method: deliveryMethod
    };

    emailjs.send('service_vxg8mkr', 'template_r2j5egm', emailParams, 'Ms0M8vSz4qw-gSxHo')
      .then(response => {
        toast.success("Comanda a fost confirmat i email-ul a fost trimis!");
        setLoading(false);
        setCart([]);
        localStorage.removeItem("cart");
        navigate("/");
      }, err => {
        toast.error("Eec la trimiterea email-ului.");
        setLoading(false);
      });
  };







  const incrementQuantity = (pid) => {
    const updatedCart = cart.map((item) => {
      if (item._id === pid) {
        return { ...item, quantity: item.quantity + 1 };
      }
      return item;
    });
    setCart(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart));
  };

  const decrementQuantity = (pid) => {
    const updatedCart = cart.map((item) => {
      if (item._id === pid && item.quantity > 1) {
        return { ...item, quantity: item.quantity - 1 };
      }
      return item;
    });
    setCart(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart));
  };
  const handlePayment = async () => {
    try {
      setLoading(true);
      const { nonce } = await instance.requestPaymentMethod();
      const { data } = await axios.post("/api/v1/product/braintree/payment", {
        nonce,
        cart,
      });
      setLoading(false);
      localStorage.removeItem("cart");
      setCart([]);
      navigate("/dashboard/user/orders");
      toast.success("Payment Completed Successfully ");
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };


  const getValueByLang = (item, key) => {
    const language = i18n.language;
    return language === 'ro' ? item[`${key}_ro`] : item[`${key}_ru`];
  };



  const handleDeliveryMethodChange = (method, e) => {
    e.preventDefault(); // Prevenim orice comportament implicit
    setDeliveryMethod(method);
  };

  return (



    <Layout title={t(`${currentPage}:title`)}>
      <div className="cart container">
        <div className="cart__row cart__row--top-message">
          <h1 className="cart__title">{t(`${currentPage}:salut`)}{` ${auth?.token && auth?.user?.name}`}</h1>
          <h4 className="cart__product-count"> {cart.length ? `${t(`${currentPage}:cart_count`, { count: cart.length })}${auth?.token ? "" : t(`${currentPage}:login_prompt`)}` : t(`${currentPage}:cart_empty`)}
          </h4>
        </div>
        <div className="cart__flex">

          <div className="cart__row--product-price">
            {cart.map((item, index) => (
              <div key={`${item._id}-${item.selectedSize}`} className="productt">
                <Link to={`/product/${item.slug}`} className="productt__photo">
                  <img src={`/api/v1/product/product-photo/${item.slug}`} className="productt__photo--top" alt={getValueByLang(item, 'name')} width="300px" height="150px" />
                </Link>
                <div className="productt__cardd">
                  <Link to={`/product/${item.slug}`} className="productt__name">{getValueByLang(item, 'name')}</Link>
                  <h4 className="productt__details">
                    {t(`${currentPage}:Mrimeaselectat`)} {item.selectedSize || "N/A"} | {t(`${currentPage}:pret`)} {item.selectedPrice ? `${item.selectedPrice} ${t(`${currentPage}:currency`)}` : `N/A`}
                  </h4>
                  <div className="quantity">
                    <button onClick={() => decrementQuantity(item._id)} className="product__quantity-btn product__quantity-btn--decrease">-</button>
                    <input className="product__quantity-input mobile" value={item.quantity} readOnly />
                    <button onClick={() => incrementQuantity(item._id)} className="product__quantity-btn product__quantity-btn--increase">+</button>
                    <button onClick={() => removeCartItem(item._id, item.selectedSize)} className="product__button-remove btn btn-outline-danger">
      {t(`${currentPage}:elimina`)}
    </button>
                  </div>
                </div>
              </div>
            ))}


            <div className="delivery-methods">
              <h2 className="delivery-methods__title">{t(`${currentPage}:delivery`)}</h2>
              <div className="delivery-methods__options">
                <div className="delivery-methods__option" onClick={(e) => handleDeliveryMethodChange("Curier i-sleep", e)} >
                  <input type="radio" id="isleepCourier" name="delivery" className="delivery-methods__input" checked={deliveryMethod === "Curier i-sleep"} onChange={() => { }} />
                  <label className="delivery-methods__label">
                    <div className="delivery-methods__info">
                      <div className="delivery-methods__icon delivery-methods__icon--sleep">
                        <img src="/images/courier.png" />
                      </div>
                      <div className="delivery-methods__text">
                        <NavLink to="/delivery"><h2 className="delivery-methods__option-title isleep">{t(`${currentPage}:curier`)}</h2></NavLink>
                        <h4 className="delivery-methods__option-description">{t(`${currentPage}:produse`)}</h4>
                      </div>
                    </div>
                  </label>
                </div>

                <div className="delivery-methods__option" onClick={(e) => handleDeliveryMethodChange("Ridicare personal", e)}>
                  <input type="radio" id="personalPickup" name="delivery" className="delivery-methods__input" checked={deliveryMethod === "Ridicare personal"} onChange={() => { }} />
                  <label htmlFor="personalPickup" className="delivery-methods__label">
                    <div className="delivery-methods__info">
                      <div className="delivery-methods__icon delivery-methods__icon--pickup">
                        <img src="/images/single.png" />
                      </div>
                      <div className="delivery-methods__text">
                        <h2 className="delivery-methods__option-title">{t(`${currentPage}:ridicare`)}</h2>
                        <h4 className="delivery-methods__option-description">{t(`${currentPage}:ridicare2`)}</h4>
                      </div>
                    </div>
                  </label>
                </div>
              </div>
              <p className="product__disclaimer cart__disclaimer">{t(`${currentPage}:diclaimer`)}</p>
            </div>
          </div>

          <div className="total">
            <h1>{t(`${currentPage}:total`)}</h1>
            <hr />
            <h2>{t(`${currentPage}:total`)}: {totalPrice()} {t(`${currentPage}:currency`)}</h2>
            {auth?.token && cart?.length > 0 && auth?.user?.address ? (
              <>
                <button className="button button--primary placesorder" onClick={handlePlaceOrder} disabled={loading || !cart.length}>
                  {loading ? t(`${currentPage}:processing`) : t(`${currentPage}:place_order`)}
                </button>
              </>
            ) : (
              <div className="address-update">
                <button className="button button--secondary " onClick={() => navigate("/dashboard/user")}>
                  {t(`${currentPage}:adaug`)}
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </Layout>




  );
};

export default CartPage;
