import React, { useState, useEffect } from "react";
import AdminMenu from "../../components/Layout/AdminMenu";
import Layout from "./../../components/Layout/Layout";
import axios from "axios";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";

const Products = () => {
  const [products, setProducts] = useState([]);

  // Obine toate produsele de la API
  const getAllProducts = async () => {
    try {
      const { data } = await axios.get("/api/v1/product/get-product");
      if (data.success) {
        setProducts(data.products);
      } else {
        toast.error("A euat obinerea produselor.");
      }
    } catch (error) {
      console.error("Eroare la obinerea produselor:", error);
      toast.error("Ceva nu a funcionat: " + error.message);
    }
  };

  // Solicit produsele cnd componenta se monteaz
  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <Layout>
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9">
            <h1>Lista tuturor produselor</h1>
            <table className="table">
              <thead>
                <tr>
                  <th>Nume Ro</th>
                  <th>Nume Ru</th>
                  <th>Categorie</th>
                  <th>Aciuni</th>  
                </tr>
              </thead>
              <tbody>
                {products.map((product) => (
                  <tr key={product._id}>
                    <td>{product.name_ro}</td>
                    <td>{product.name_ru}</td>
                    <td>{product.category ? product.category.nameRo : 'Necunoscut'}</td>
                    <td>
                      <Link to={`/dashboard/admin/product/${product.slug}`}>Actualizeaz</Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Products;
