import React, { useState, useEffect } from 'react';
import Layout from './../components/Layout/Layout';
import { useCart } from "../context/cart";
import { toast } from 'react-hot-toast';
import { Link } from "react-router-dom"; 
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};

const ProductDetails = () => {
 

   
  const params = useParams();
  // const navigate = useNavigate();
 const { t } = useTranslation();
  const currentPage = 'ProductDetails';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const handleLanguageChange = () => {
      const currentLang = localStorage.getItem('i18nextLng') || 'ro';
      if (i18n.language !== currentLang) {
        i18n.changeLanguage(currentLang);
      }
    };
  
    // Ascult pentru schimbri de limb
    i18n.on('languageChanged', handleLanguageChange);
  
    // Cleanup la demontare
    return () => {
      i18n.off('languageChanged', handleLanguageChange);
    };
  }, []);
  useEffect(() => {
    loadPageTranslations(i18n.language, currentPage).then(loaded => {
      if (loaded) {
        // console.log('Traducerile pentru pagina curent au fost ncrcate.');
      }
    });
  }, [i18n.language]);
  
  const MyComponent = () => {
    const { i18n } = useTranslation();
  
    useEffect(() => {
      console.log("Limba curent este:", i18n.language);
    }, [i18n.language]);
  
  
  };

  const currentLang = i18n.language;
  const [product, setProduct] = useState({});
  const [relatedProducts, setRelatedProducts] = useState([]);
  const [quantity, setQuantity] = useState(1); // Stare nou pentru cantitate
  const [selectedSize, setSelectedSize] = useState({ size: '', price: 0 });
  const [cart, setCart] = useCart();// presupunem c useCart este un hook personalizat pentru gestionarea coului
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isOpen, setIsOpen] = useState({
    important: false,
    characteristics: false,
    technicalStructure: false,
  });
  useEffect(() => {
    if (params?.slug) {
      getProduct();
    }
  }, [params?.slug]);

  const getProduct = async () => {
    try {
      const { data } = await axios.get(`/api/v1/product/get-product/${params.slug}`);
 

      setProduct(data?.product);
      
      if (data?.product.sizes?.length) {
        setSelectedSize({
          size: data.product.sizes[0].size,
          price: data.product.sizes[0].price,
        });
      }
      getSimilarProduct(data?.product._id, data?.product.category._id);
    } catch (error) {
      console.log(error);
    }
  };

  const getSimilarProduct = async (pid, cid) => {
    try {
      const { data } = await axios.get(`/api/v1/product/related-product/${pid}/${cid}`);
      setRelatedProducts(data?.products);
    } catch (error) {
      console.log(error);
    }
  };

  const handleSizeChange = (event) => {
    const size = event.target.value;
    const selected = product.sizes.find(s => s.size === size);
    setSelectedSize({
      size: size,
      price: selected.price,
    });
  };

  // Modificat pentru a include mrimea i preul selectat
  const addToCart = () => {
    const existingProductIndex = cart.findIndex(
      (item) => item._id === product._id && item.selectedSize === selectedSize.size
    );

    if (existingProductIndex >= 0) {
      // Produsul exist deja n co, actualizm cantitatea
      const updatedCart = cart.map((item, index) => {
        if (index === existingProductIndex) {
          return {
            ...item,
            quantity: item.quantity + quantity,
          };
        }
        return item;
      });
      setCart(updatedCart);
      localStorage.setItem("cart", JSON.stringify(updatedCart));
    } else {
      // Produsul nu exist n co, l adugm
      const productToAdd = {
        ...product,
        selectedSize: selectedSize.size,
        selectedPrice: selectedSize.price,
        quantity: quantity,
      };
      const updatedCart = [...cart, productToAdd];
      setCart(updatedCart);
      localStorage.setItem("cart", JSON.stringify(updatedCart));
    }
    toast.success("Product added to cart!");
  };
  const toggleSection = (section) => {
    setIsOpen((prev) => ({ ...prev, [section]: !prev[section] }));
  };
  const showModal = () => {
    setIsModalVisible(true);
  };

  const closeModal = () => {
    setIsModalVisible(false);
  };
  const getLocalizedCategoryName = (category) => {
    if (!category) return '';
    return currentLang === 'ro' ? category.nameRo : category.nameRu;
  };
  const getLocalizedInfo = (info, key) => {
    return currentLang === 'ro' ? info[key + '_ro'] : info[key + '_ru'];
  };
  
  const getValueByLang = (item, key) => {
    const language = i18n.language;
    return language === 'ro' ? item[`${key}_ro`] : item[`${key}_ru`];
  };
  
  return (
    <Layout title={currentLang === 'ro' ? product.nameRo : product.nameRu}>

      <div className="product container">
        <div className="product__row">
          <div className="product__image-section">
          {product && product.slug ? (
  <img src={`/api/v1/product/product-photo/${product.slug}`} alt={product.name} className="product__image" />
) : (
  <p>Loading...</p>
)}
          
            {/* <img
              src={`/api/v1/product/product-photo/${product.slug}`}
              alt={product.name}
              className="product__image"
            /> */}
          </div>
          <div className="product__information">
          <h1 className="product__title">
            {currentLang === 'ro' ? product.name_ro : product.name_ru}
          </h1>
          <p className="product__category">
          {t(`${currentPage}:categorie`)} {getLocalizedCategoryName(product.category)}
        </p>
            <div className="product__size-quantity">
              {/* Size Selector Element */}
              <div className="product__size-selector">
                <label htmlFor="size-select" className="product__size-label">{t(`${currentPage}:marime`)}</label>
                <select
                  id="size-select"
                  value={selectedSize.size}
                  onChange={handleSizeChange}
                  className="product__size-select"
                >
                  {product.sizes?.map((s, index) => (
                    <option key={index} value={s.size}>{s.size} - {s.price} {t(`${currentPage}:lei`)}</option>
                  ))}
                </select>

              </div>


              {/* Quantity Selector Element */}
              <div className="product__quantity-selector">
                <button className="product__quantity-btn product__quantity-btn--decrease" onClick={() => setQuantity((prevQuantity) => Math.max(1, prevQuantity - 1))}>-</button>
                <input
                  type="text"
                  id="quantity"
                  name="quantity"
                  value={quantity}
                  readOnly
                  className="product__quantity-input"
                />
                <button className="product__quantity-btn product__quantity-btn--increase" onClick={() => setQuantity((prevQuantity) => prevQuantity + 1)}>+</button>
              </div>
            </div>

            {/* <!-- Delivery Options Element --> */}
            <div className="product__delivery-options">
              <div className="product__delivery-options-item">
                <div className="product__delivery-options-icon">
                  {/* <!-- Inserai imaginea pentru Ridicare personal --> */}
                  <img src="../images/pin.svg" alt="" />
                </div>
                <div className="product__delivery-options-info">
                  <span className="product__delivery-options-text">{t(`${currentPage}:ridicare`)}&nbsp;{t(`${currentPage}:personala`)}</span>
                  <span className="product__delivery-options-dots">&nbsp;..................&nbsp;</span>
                  <span className="product__delivery-options-status">{t(`${currentPage}:gratuit`)}</span>
                </div>
              </div>
              <div className="product__delivery-options-item">
                <div className="product__delivery-options-icon">
                  {/* <!-- Inserai imaginea pentru Livrare n Moldova --> */}
                  <img src="../images/car.svg" alt="" />
                </div>
                <div className="product__delivery-options-info">
                  <span className="product__delivery-options-text">{t(`${currentPage}:livrare`)}&nbsp;{t(`${currentPage}:in`)}&nbsp;{t(`${currentPage}:Moldova`)}</span>
                  <span className="product__delivery-options-dots">&nbsp;&nbsp;..................&nbsp;</span>
                  <Link to="/delivery"><span className="product__delivery-options-status-con">{t(`${currentPage}:concretizai`)}</span></Link>
                </div>
              </div>
            </div>
            <div className="product__price-row">
              <h1 className="product__price">{t(`${currentPage}:pret`)}:&nbsp;{selectedSize.price}&nbsp;{t(`${currentPage}:lei`)} </h1>
              <button className="product__add-to-cart-btn button" onClick={addToCart}>{t(`${currentPage}:adauga`)}&nbsp;{t(`${currentPage}:in`)}&nbsp;{t(`${currentPage}:cos`)}</button>
            </div>
            <div className="product__size-individual">
              <div className="product__size-individual-image">
                <img src="../images/size-individual.svg" alt="" />
              </div>
              <div className="product__size-individual-title">
                <p onClick={showModal}>{t(`${currentPage}:marimi_individuale`)}</p>
              </div>

              {isModalVisible && (
                <div className="modal">
                  <div className="modal__content">
                    <span className="modal__close" onClick={closeModal}>&times;</span>
                    <h4>{t(`${currentPage}:tarife`)}</h4>
                    <li>{t(`${currentPage}:tarife1`)}</li>
                    <li>{t(`${currentPage}:tarife2`)}</li>
                    <li>{t(`${currentPage}:tarife3`)}</li>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
        <div className="product__info">
          {/* <!-- Add to Cart Button --> */}
          <div className="product__title-compart">
            <p>{t(`${currentPage}:descriere`)}</p>
          </div>
          <p className="product__description">
            {currentLang === 'ro' ? product.description_ro : product.description_ru}
          </p>
                    {/* Collapsible Sections for Product Specifications */}
          <div className="product__specifications">
            <div className="product__specification-section" onClick={() => toggleSection('technicalStructure')}>
              <div className="product__specification-top">
                <div className="product__specification-title">{t(`${currentPage}:technical`)}</div>
                <div className="product__specification-toggle">
                  {isOpen.technicalStructure ? '-' : '+'}
                </div>
              </div>
              {isOpen.technicalStructure && product.technical_structure && product.technical_structure.length > 0 && (
  <ul className="product__specification-list">
    {product.technical_structure.map((tech, index) => (
      <li className="product__specification-item" key={index}>
        {getLocalizedInfo(tech, 'technical_structurespec')}
        
      </li>
    ))}
  </ul>
)}
            </div>
           
           
           
           
            <div className="product__specification-section" onClick={() => toggleSection('important')}>
              <div className="product__specification-top">
                <div className="product__specification-title">{t(`${currentPage}:important`)}</div>
                <div className="product__specification-toggle">
                  {isOpen.important ? '-' : '+'}
                </div>
              </div>
              {isOpen.important && (
                <ul className="product__specification-list">
                {product.important?.map((item, index) => (
                      <li className="product__specification-item" key={index}>{getLocalizedInfo(item, 'importantspec')}</li>
                    ))}
                </ul>
              )}
            </div>
           
           
           
           
           
           
           
            <div className="product__specification-section" onClick={() => toggleSection('characteristics')}>
              <div className="product__specification-top">
                <div className="product__specification-title">{t(`${currentPage}:caracteristici`)}</div>
                <div className="product__specification-toggle">
                  {isOpen.characteristics ? '-' : '+'}
                </div>
              </div>
              {isOpen.characteristics && (
                <table className="product__specification-table">
                 
                  <tbody className="product__specification-tbody">
                  {product.characteristics?.map((char, index) => (
    <tr className="product__specification-tr" key={index}>
      <td className="product__specification-td product__specification-td--key">{currentLang === 'ro' ? char.key_ro : char.key_ru}</td>
      <td className="product__specification-td product__specification-td--value">{currentLang === 'ro' ? char.value_ro : char.value_ru}</td>
    </tr>
  ))}
                  </tbody>
                </table>
              )}
              {/* <!-- Disclaimer Text --> */}
            </div>
          </div>
          <p className="product__disclaimer">
          {t(`${currentPage}:datorita`)}
          </p>
        </div>
      </div>
      {/* <!-- Similar Products Section --> */}
      <hr className="product__separator" />
      <div className="product__related container">
        <h6 className="product__related-title">{t(`${currentPage}:similare`)}</h6>
        {relatedProducts.length < 1 && (
          <p className="product__related-disclaimer">{t(`${currentPage}:nugas`)}</p>
        )}


        <div className="product__related-list">
        {relatedProducts.map((relatedProduct) => (
                <div className="product__card " key={relatedProduct._id}>
                  <Link to={`/product/${relatedProduct.slug}`} className="product__card-link" rel="noopener noreferrer">
                    
                    <img
                      src={`/api/v1/product/product-photo/${relatedProduct.slug}`}
                      className="product__card-image card-img-top"
                      alt={relatedProduct.name}
                    />
                  </Link>
                  <div className="product__card-body ">
                    <Link to={`/product/${relatedProduct.slug}`} className="product__card-title card-title" rel="noopener noreferrer">
                      {getValueByLang(relatedProduct, 'name')}
                    </Link>
                    <ul className="product__card-features">
                      {relatedProduct.characteristics.slice(0, 4).map((char, index) => (
                        <li className="product__card-feature" key={index}>
                          {getValueByLang(char, 'key')}: {getValueByLang(char, 'value')}
                        </li>
                      ))}
                    </ul>
                    <p className="product__card-price card-text">
                    {t(`${currentPage}:dela`)}  {relatedProduct.sizes && relatedProduct.sizes.length > 0 ? relatedProduct.sizes[0].price : "N/A"} {t(`${currentPage}:lei`)}
                    </p>
                  </div>
                </div>
              ))}
        </div>
      </div>
    </Layout>

  );
};

export default ProductDetails;
