import React, { useEffect, useState } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "./../../components/Layout/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
// import CategoryForm from "../../components/Form/CategoryForm";
import { Modal } from "antd";

const CreateCategory = () => {
  const [categories, setCategories] = useState([]);
  const [nameRo, setNameRo] = useState(""); // Nume pentru romn
  const [nameRu, setNameRu] = useState(""); // Nume pentru rus
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updatedNameRo, setUpdatedNameRo] = useState(""); // Nume actualizat pentru romn
  const [updatedNameRu, setUpdatedNameRu] = useState(""); // Nume actualizat pentru rus
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/get-category");
      if (data.success) {
        // Verific aici c foloseti cheia corect din rspunsul API
        setCategories(data.category || []);
      } else {
        setCategories([]);
      }
    } catch (error) {
      console.error(error);
      toast.error("A aprut o eroare la obinerea categoriilor");
      setCategories([]);
    }
  };
  
  

  useEffect(() => {
    getAllCategory();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!nameRo || !nameRu) {
      toast.error("Ambele cmpuri sunt necesare.");
      return;
    }
    try {
      const { data } = await axios.post("/api/v1/category/create-category", {
        nameRo, nameRu
      });
      if (data.success) {
        toast.success("Categoriile au fost create cu succes");
        setNameRo("");
        setNameRu("");
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.error(error);
      toast.error("A aprut o eroare la trimiterea formularului");
    }
  };

  const handleUpdate = async (e) => {
    e.preventDefault();
    if (!selected) return;
    try {
      const { data } = await axios.put(`/api/v1/category/update-category/${selected._id}`, {
        nameRo: updatedNameRo, 
        nameRu: updatedNameRu
      });
      if (data.success) {
        toast.success("Categoria a fost actualizat cu succes");
        setSelected(null);
        setUpdatedNameRo("");
        setUpdatedNameRu("");
        setVisible(false);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.error(error);
      toast.error("A aprut o eroare la actualizare");
    }
  };

  const handleDelete = async (id) => {
    try {
      const { data } = await axios.delete(`/api/v1/category/delete-category/${id}`);
      if (data.success) {
        toast.success("Categoria a fost tears cu succes");
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.error(error);
      toast.error("A aprut o eroare la tergere");
    }
  };

  const handleEdit = (category) => {
    setSelected(category);
    setUpdatedNameRo(category.nameRo);
    setUpdatedNameRu(category.nameRu);
    setVisible(true);
  };

  return (
    <Layout title={"Tablou de bord - Creare categorie"}>
    <div className="container-fluid ">
      <div className="row">
        <div className="col-md-3">
          <AdminMenu />
        </div>
        <div className="col-md-9">
          <h1>Administreaz Categorii</h1>
          <div className="p-3 w-50">
            <form onSubmit={handleSubmit}>
              <div className="mb-3">
                <label htmlFor="nameRo" className="form-label">Nume (RO)</label>
                <input
                  type="text"
                  className="form-control"
                  id="nameRo"
                  value={nameRo}
                  onChange={(e) => setNameRo(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <label htmlFor="nameRu" className="form-label">Nume (RU)</label>
                <input
                  type="text"
                  className="form-control"
                  id="nameRu"
                  value={nameRu}
                  onChange={(e) => setNameRu(e.target.value)}
                />
              </div>
              <button type="submit" className="btn btn-primary">Trimite</button>
            </form>
          </div>
          {/* Aici afim tabelul cu categoriile existente */}
          <div className="w-75">
  <table className="table">
    <thead>
      <tr>
        <th scope="col">Nume (RO)</th>
        <th scope="col">Nume (RU)</th>
        <th scope="col">Aciuni</th>
      </tr>
    </thead>
    <tbody>
      {categories.length > 0 ? (
        categories.map((c) => (
          <tr key={c._id}>
            <td>{c.nameRo}</td>
            <td>{c.nameRu}</td>
            <td>
              <button
                className="btn btn-primary ms-2"
                onClick={() => {
                  setVisible(true);
                  setUpdatedNameRo(c.nameRo);
                  setUpdatedNameRu(c.nameRu);
                  setSelected(c);
                }}
              >
                Editeaz
              </button>
              <button
                className="btn btn-danger ms-2"
                onClick={() => handleDelete(c._id)}
              >
                terge
              </button>
            </td>
          </tr>
        ))
      ) : (
        <tr>
          <td colSpan="3" className="text-center">Nu exist categorii adugate nc.</td>
        </tr>
      )}
    </tbody>
  </table>
</div>



<Modal
  title="Edit Category"
  visible={visible}
  onOk={handleUpdate}
  onCancel={() => setVisible(false)}
  footer={[
    <button key="back" onClick={() => setVisible(false)} className="btn btn-secondary">
      Cancel
    </button>,
    <button key="submit" type="submit" className="btn btn-primary" onClick={handleUpdate}>
      Update
    </button>,
  ]}
>
  <form>
    <div className="form-group">
      <label htmlFor="updatedNameRo">"Name (RO)"</label>
      <input
        type="text"
        className="form-control"
        id="updatedNameRo"
        value={updatedNameRo}
        onChange={(e) => setUpdatedNameRo(e.target.value)}
      />
    </div>
    <div className="form-group">
      <label htmlFor="updatedNameRu">"Name (RU)"</label>
      <input
        type="text"
        className="form-control"
        id="updatedNameRu"
        value={updatedNameRu}
        onChange={(e) => setUpdatedNameRu(e.target.value)}
      />
    </div>
  </form>
</Modal>

          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreateCategory;


