import React, { useState, useEffect } from "react";
import { useTranslation } from 'react-i18next';
import Layout from "./../components/Layout/Layout";
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};

