import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import HttpBackend from 'i18next-http-backend'; // Pentru ncrcarea traducerilor prin HTTP
import LanguageDetector from 'i18next-browser-languagedetector'; // Pentru detectarea limbii browserului

i18n
  .use(HttpBackend)  // ncarc traducerile folosind backend HTTP
  .use(LanguageDetector)  // detecteaz limba din browser
  .use(initReactI18next)  // iniializeaz react-i18next
  .init({
    fallbackLng: 'ro', // Limba de rezerv, folosit cnd nu este disponibil limba detectat
    supportedLngs: ['ro', 'ru'], // Limbile suportate
    preload: ['ro', 'ru'], // Prencarc limbile specificate pentru o performan mbuntit
    detection: {
      order: ['localStorage', 'cookie', 'navigator', 'querystring', 'htmlTag'], // Ordinea metodelor de detectare
      caches: ['localStorage'], // Limba selectat va fi pstrat n localStorage
      lookupLocalStorage: 'i18nextLng', // Cheia sub care se salveaz limba n localStorage
      lookupCookie: 'i18n_lng', // Numele cookie-ului pentru limb
      cookieMinutes: 10080, // Timpul de expirare a cookie-ului (7 zile)
      htmlTag: document.documentElement, // Utilizeaz tag-ul html pentru a seta atributul lang
    },
    keySeparator: false, // Dezactiveaz utilizarea separatorului pentru chei
    interpolation: {
      escapeValue: false, // Nu este necesar escaparea valorilor n React
    },
    backend: {
      loadPath: '/translations/{{lng}}/{{ns}}.json', // Calea ctre fiierele de traduceri
      reloadInterval: false, // Nu rencrca resursele de limb periodic
      onLoad: (response) => console.log('Load response:', response) // Logheaz rspunsul la ncrcarea traducerilor
    },
    react: {
      useSuspense: true, // Dezactiveaz Suspense pentru ncrcarea traducerilor
      // wait: true // Ateapt ncrcarea resurselor de limb nainte de a randa componentele
    },
    debug: false, // Activeaz modul debug pentru a vedea loguri detaliate n consola browserului
  });

export default i18n;
