import React, { useState, useEffect } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "./../../components/Layout/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { Button, Input } from "antd";
import { useNavigate } from "react-router-dom";
const predefinedSizes = [
  { size: "60x120", price: "" },
  { size: "70x190", price: "" },
  { size: "70x200", price: "" },
  { size: "80x190", price: "" },
  { size: "80x200", price: "" },
  { size: "90x190", price: "" },
  { size: "90x200", price: "" },
  { size: "100x190", price: "" },
  { size: "100x200", price: "" },
  { size: "120x190", price: "" },
  { size: "120x200", price: "" },
  { size: "130x190", price: "" },
  { size: "130x200", price: "" },
  { size: "140x190", price: "" },
  { size: "140x200", price: "" },
  { size: "150x190", price: "" },
  { size: "150x200", price: "" },
  { size: "160x190", price: "" },
  { size: "160x200", price: "" },
  { size: "180x190", price: "" },
  { size: "180x200", price: "" },
  { size: "200x200", price: "" }
];
const CreateProduct = () => {
  const navigate = useNavigate();
  const [kg, setKg] = useState("");
  const [categories, setCategories] = useState([]);
  const [nameRo, setNameRo] = useState("");
  const [nameRu, setNameRu] = useState("");
  const [descriptionRo, setDescriptionRo] = useState("");
  const [descriptionRu, setDescriptionRu] = useState("");
  const [category, setCategory] = useState("");
  const [sizes, setSizes] = useState(predefinedSizes); // Initialize sizes with predefined values
  const [important, setImportant] = useState([{ importantspec: "" }]);
  const [characteristics, setCharacteristics] = useState([{ key: "", value: "" }]);
  const [technicalStructure, setTechnicalStructure] = useState([{ technical_structurespec: "" }]);
  const [photo, setPhoto] = useState(null);

  // Fetch all categories
  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/get-category");
      // console.log("Data fetched from API:", data); // Log pentru a vedea datele
      if (data?.success) {
        setCategories(data.category); // Folosete 'data.category', nu 'data.categories'
      } else {
        toast.error("Failed to fetch categories. Please check the API or network!");
        setCategories([]);
      }
    } catch (error) {
      console.error("Error fetching categories:", error);
      toast.error("An error occurred while fetching categories");
      setCategories([]);
    }
  };
  
  

  useEffect(() => {
    getAllCategories();
  }, []);

  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      const formData = new FormData();
      formData.append("name_ro", nameRo);
      formData.append("name_ru", nameRu);
      formData.append("description_ro", descriptionRo);
      formData.append("description_ru", descriptionRu);
      formData.append("category", category);
      formData.append("kg", kg);
      formData.append("sizes", JSON.stringify(sizes));
      formData.append("important", JSON.stringify(important));
      formData.append("characteristics", JSON.stringify(characteristics));
      formData.append("technical_structure", JSON.stringify(technicalStructure));
      if (photo) {
        formData.append("photo", photo);
      }
      console.log([...formData]); // Acesta va afia toate perechile de chei/valori din formData
      const response = await axios.post("/api/v1/product/create-product", formData);
      const data = response.data;

      if (data?.success) {
        toast.success("Product created successfully");
        navigate("/dashboard/admin/products");
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.error(error);
      toast.error("Something went wrong");
    }
  };
  const handleSizeChange = (index, field, value) => {
    const newSizes = [...sizes];
    newSizes[index] = { ...newSizes[index], [field]: value };
    setSizes(newSizes);
  };

  const addSize = () => setSizes([...sizes, { size: "", price: "" }]);
  const removeSize = index => setSizes(sizes.filter((_, i) => i !== index));

  const addCharacteristic = () => setCharacteristics([...characteristics, { key: "", value: "" }]);
  const removeCharacteristic = index => setCharacteristics(characteristics.filter((_, i) => i !== index));

  const addImportantNote = () => setImportant([...important, { importantspec: "" }]);
  const removeImportantNote = index => setImportant(important.filter((_, i) => i !== index));

  const addTechnicalStructure = () => setTechnicalStructure([...technicalStructure, { technical_structurespec: "" }]);
  const removeTechnicalStructure = index => setTechnicalStructure(technicalStructure.filter((_, i) => i !== index));

  const handleCharacteristicChange = (index, field, value) => {
    const updatedCharacteristics = [...characteristics];
    updatedCharacteristics[index] = { ...updatedCharacteristics[index], [field]: value };
    setCharacteristics(updatedCharacteristics);
  };
  
  const handleImportantChange = (index, field, value) => {
    const updatedImportant = [...important];
    updatedImportant[index] = { ...updatedImportant[index], [field]: value };
    setImportant(updatedImportant);
  };
  
  const handleTechnicalStructureChange = (index, field, value) => {
    const updatedTechnicalStructure = [...technicalStructure];
    updatedTechnicalStructure[index] = { ...updatedTechnicalStructure[index], [field]: value };
    setTechnicalStructure(updatedTechnicalStructure);
  };
  
  return (
    <Layout title="Dashboard - Create Product">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9">
            <h1>Create Product</h1>
            <form onSubmit={handleCreate}>
              <Input placeholder="Name RO" value={nameRo} onChange={e => setNameRo(e.target.value)} />
              <Input placeholder="Name RU" value={nameRu} onChange={e => setNameRu(e.target.value)} />
              <Input placeholder="Description RO" value={descriptionRo} onChange={e => setDescriptionRo(e.target.value)} />
              <Input placeholder="Description RU" value={descriptionRu} onChange={e => setDescriptionRu(e.target.value)} />
              <select value={category} onChange={e => setCategory(e.target.value)} style={{ width: '100%', marginBottom: '20px' }}>
                <option value="">Select Category</option>
                {categories.map(c => (
                  <option key={c._id} value={c._id}>{c.nameRo}</option> // Folosete 'c.nameRo' pentru a afia numele categoriei n romn
                ))}
              </select>
              <Input placeholder="Weight (kg)" value={kg} onChange={e => setKg(e.target.value)} type="number" style={{ marginBottom: '20px' }} />
              {sizes.map((size, index) => (
                <div key={index}>
                  <Input placeholder="Size" value={size.size} onChange={e => handleSizeChange(index, 'size', e.target.value)} />
                  <Input placeholder="Price" type="number" value={size.price} onChange={e => handleSizeChange(index, 'price', e.target.value)} />
                  <Button onClick={() => removeSize(index)}>Remove Size</Button>
                </div>
              ))}
              <Button onClick={addSize}>Add Size</Button>
              {/* Technical Structure Inputs */}
              {technicalStructure.map((tech, index) => (
                <div key={index}>
                  <Input
                    placeholder="Technical Structure Spec RO"
                    value={tech.technical_structurespec_ro}
                    onChange={e => handleTechnicalStructureChange(index, 'technical_structurespec_ro', e.target.value)}
                  />
                  <Input
                    placeholder="Technical Structure Spec RU"
                    value={tech.technical_structurespec_ru}
                    onChange={e => handleTechnicalStructureChange(index, 'technical_structurespec_ru', e.target.value)}
                  />
                  <Button onClick={() => removeTechnicalStructure(index)}>Remove Technical Structure</Button>
                </div>
              ))}
              <Button onClick={addTechnicalStructure}>Add Technical Structure</Button>
              {/* Important Inputs */}
              {important.map((imp, index) => (
                <div key={index}>
                  <Input
                    placeholder="Important Spec RO"
                    value={imp.importantspec_ro}
                    onChange={e => handleImportantChange(index, 'importantspec_ro', e.target.value)}
                  />
                  <Input
                    placeholder="Important Spec RU"
                    value={imp.importantspec_ru}
                    onChange={e => handleImportantChange(index, 'importantspec_ru', e.target.value)}
                  />
                  <Button onClick={() => removeImportantNote(index)}>Remove Important Spec</Button>
                </div>
              ))}
              <Button onClick={addImportantNote}>Add Important Spec</Button>
             
               {/* Characteristics Inputs */}
               {characteristics.map((char, index) => (
                <div key={index}>
                  <Input
                    placeholder="Characteristic Key RO"
                    value={char.key_ro}
                    onChange={e => handleCharacteristicChange(index, 'key_ro', e.target.value)}
                  />
                  <Input
                    placeholder="Characteristic Key RU"
                    value={char.key_ru}
                    onChange={e => handleCharacteristicChange(index, 'key_ru', e.target.value)}
                  />
                  <Input
                    placeholder="Characteristic Value RO"
                    value={char.value_ro}
                    onChange={e => handleCharacteristicChange(index, 'value_ro', e.target.value)}
                  />
                  <Input
                    placeholder="Characteristic Value RU"
                    value={char.value_ru}
                    onChange={e => handleCharacteristicChange(index, 'value_ru', e.target.value)}
                  />
                  <Button onClick={() => removeCharacteristic(index)}>Remove Characteristic</Button>
                </div>
              ))}
              <Button onClick={addCharacteristic}>Add Characteristic</Button>
              <label className="btn btn-outline-secondary col-md-12">
                {photo ? photo.name : "Upload Photo"}
                <input type="file" name="photo" accept="image/*" onChange={(e) => setPhoto(e.target.files[0])} hidden />
              </label>
              <Button type="primary" htmlType="submit">Create Product</Button>
            </form>
          </div>
        </div>
      </div>
    </Layout>

  );

};


export default CreateProduct;
