import React, { useState,useEffect } from 'react';
import Layout from '../../components/Layout/Layout';
import  toast  from 'react-hot-toast';
import axios from 'axios';
import {useNavigate} from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';
const loadPageTranslations = (lang, page) => {
    return import(`../../translations/${lang}/${page}.json`)
      .then(resources => {
        i18n.addResourceBundle(lang, page, resources.default);
        return true; // Indic faptul c ncrcarea a reuit
      })
      .catch(err => {
        console.error("Failed to load translations", err);
        return false; // Indic un eec n ncrcarea traducerilor
      });
  };
const Register = () => {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [phone, setPhone] = useState("");
    const [address, setAddress] = useState("");
    const [answer, setAnswer] = useState("");
    const navigate= useNavigate();
    const { t } = useTranslation();
    const currentPage = 'Register';
    const [translationsLoaded, setTranslationsLoaded] = useState(false);
  
    useEffect(() => {
      const currentLang = i18n.language;
      loadPageTranslations(currentLang, currentPage).then((loaded) => {
        if (loaded) {
          setTranslationsLoaded(true); 
        }
      });
    }, []);

    // form function
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const res = await axios.post(`/api/v1/auth/register`, { name, email, password, phone, address, answer });
        if(res && res.data.success){
            toast.success(res.data && res.data.message);
            navigate("/login");
        }else{
            toast.error(res.data.message);
        }
        }

        catch (error) {
            console.log(error);
            toast.error("Somethinf went wrong");
        }
    };


    return (
        <Layout title='Register'>
        <div className="login-container">
        <div className="login-form">
        <h2 className="login-form__title">{t(`${currentPage}:reg`)}</h2>
            <form onSubmit={handleSubmit}   >
                <div className="login-form__group">
                    <input type="text" value={name} onChange={(e) => setName(e.target.value)} required placeholder={t(`${currentPage}:name`)} />
                </div>
                <div className="login-form__group">
                    <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required placeholder={t(`${currentPage}:email`)} />
                </div>
                <div className="login-form__group">
                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required placeholder={t(`${currentPage}:pass`)}/>
                </div>
                <div className="login-form__group">
                    <input type="text" value={phone} onChange={(e) => setPhone(e.target.value)} required placeholder={t(`${currentPage}:phone`)} />
                </div>
                <div className="login-form__group">
                    <input type="text" value={address} onChange={(e) => setAddress(e.target.value)} required placeholder={t(`${currentPage}:add`)} />
                </div>
                <div className="login-form__group">
                    <input type="text" value={answer} onChange={(e) => setAnswer(e.target.value)} required placeholder={t(`${currentPage}:qeus`)} />
                </div>
                <button type="submit" className="login-form__submit">{t(`${currentPage}:inreg`)}</button>
            </form>
            </div>
        </div>
    </Layout>
    )
}

export default Register