import useCategory from "../../hooks/useCategory";

import { Link } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      // console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};

  const Footer = () => {
    const { t, i18n } = useTranslation();
    const currentPage = 'Footer';
    const categories = useCategory();
    const [translationsLoaded, setTranslationsLoaded] = useState(false);

    const getCategoryName = (category) => {
      const language = i18n.language;
      return language === 'ro' ? category.nameRo : category.nameRu;
    };

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);
  
  return (

    <footer className="footer">
      <div className="footer__inner container">
        <div className="footer__logo">
          <img src="../../../images/logo.svg" alt="logo" />
        </div>
        <div className="footer__menu">
          <div className="footer__menu-item">
            <div className="footer__menu-item-title">
            <Link to={`/saltele`}>
            {t(`${currentPage}:saltele`)}
                </Link>
            </div>
            <div className="footer__menu-list">
            {categories?.map((category) => (
  <p key={category._id}> {/* Asigurai-v c utilizai un identificator unic aici */}
    <Link to={`/category/${category.slug}`}>
      {getCategoryName(category)}
    </Link>
  </p>
))}



          </div>
          </div>
          <div className="footer__menu-item">
            <div className="footer__menu-item-title">
           
                <Link to="/about">{t(`${currentPage}:desprenoi`)}</Link>
              
            </div>
            <div className="footer__menu-list">
                <Link to="/delivery">{t(`${currentPage}:livrare`)}</Link>
            </div>
            <div className="footer__menu-list">
                <Link to="/warranty">{t(`${currentPage}:garantie`)}</Link>
            </div>
            <div className="footer__menu-list">
                <Link to="/policy">{t(`${currentPage}:politica`)}</Link>
            </div>
          </div>
          <div className="footer__menu-item">
            <div className="footer__menu-item-title">
             
                <Link to="/contact">{t(`${currentPage}:contacte`)}</Link>
             
            </div>
            <div className="footer__menu-list">
        
              <Link to="tel:+37379681319">
                <p>+ 373 79 681 319</p>
              </Link>
            </div>
            <div className="footer__menu-list">
              <Link to="mailto:i.sleepmd.mattress@gmail.com">
                <p>i.sleepmd.mattress@gmail.com</p>
              </Link>
            </div>
          </div>
          <div className="footer__menu-item">
            <div className="footer__menu-item-title">
              <p>{t(`${currentPage}:grafic`)}</p>
            </div>
            <div className="footer__menu-list">
              <p>{t(`${currentPage}:luni`)}</p>
            </div>
            
            <div className="footer__menu-list">
              <p>{t(`${currentPage}:duminica`)}</p>
            </div>
          </div>
        </div>
      </div>
      <div className="footer__privacy">
        <p>  2024 {t(`${currentPage}:privacy`)} i-sleep.md</p>
      </div>
    </footer>
  );
};

export default Footer;
