import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
// import UserMenu from "../../components/Layout/UserMenu";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import axios from "axios";
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};
const Dashboard = () => {
  const { t } = useTranslation();
  const currentPage = 'Dashboard';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);
  const [auth, setAuth] = useAuth();

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  useEffect(() => {
    const { email, name, phone, address } = auth?.user;
    setName(name);
    setPhone(phone);
    setEmail(email);
    setAddress(address);
  }, [auth?.user]);
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put("/api/v1/auth/profile", {
        name,
        email,
        password,
        phone,
        address,
      });
      if (data?.errro) {
        toast.error(data?.error);
      } else {
        setAuth({ ...auth, user: data?.updatedUser });
        let ls = localStorage.getItem("auth");
        ls = JSON.parse(ls);
        ls.user = data.updatedUser;
        localStorage.setItem("auth", JSON.stringify(ls));
        toast.success("Profile Updated Successfully");
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };
  return (
    <Layout title={'Dashboard - Ecommerce App'}>
      <div className="custom-dashboard-container">
        <div className="custom-dashboard-row">
          <div className="custom-dashboard-main">
            <div className="custom-form-container">
              <form onSubmit={handleSubmit} className="custom-form">
                <h1 className="custom-title">{t(`${currentPage}:top`)}</h1>
                <div className="custom-form-group">
                  <input
                    type="text"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="custom-input"
                    placeholder="Enter Your Name"
                    autoFocus
                  />
                </div>
                <div className="custom-form-group">
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="custom-input"
                    placeholder="Enter Your Email"
                    disabled
                  />
                </div>
                <div className="custom-form-group">
                  <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="custom-input"
                    placeholder={t(`${currentPage}:pass`)}
                  />
                </div>
                <div className="custom-form-group">
                  <input
                    type="text"
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                    className="custom-input"
                    placeholder="Enter Your Phone"
                  />
                </div>
                <div className="custom-form-group">
                  <input
                    type="text"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    className="custom-input"
                    placeholder="Enter Your Address"
                  />
                </div>
                <button type="submit" className="custom-button button">
                {t(`${currentPage}:update`)}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Dashboard;