import React, { useEffect, useState } from "react";
import Layout from "./../components/Layout/Layout";
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};

const About = () => {
  const { t } = useTranslation();
  const currentPage = 'About';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);

  return (
    <Layout title={t(`${currentPage}:title`)}>
    <div className="about">
  <div className="image">
    <img src="images/about.png" alt="" />
  </div>
  <div className="middle container">
    <div className="middle__item">
      <div className="middle__title" >
        <h1 >{t(`${currentPage}:header-title1`)}</h1>
      </div>
      <div className="middle__description">
        <p>{t(`${currentPage}:description1`)}</p>
      </div>
    </div>
    <div className="middle__item">
      <div className="middle__title">
        <h1>{t(`${currentPage}:header-title2`)}</h1>
      </div>
      <div className="middle__description">
<p>{t(`${currentPage}:description2`)}</p>      
</div>
    </div>
    <div className="middle__item">
      <div className="middle__title">
        <h1>{t(`${currentPage}:header-title3`)}</h1>
      </div>
      <div className="middle__description">
      <p>{t(`${currentPage}:description3`)}</p>  
            </div>
    </div>
    <div className="middle__item">
      <div className="middle__title">
        <h1>{t(`${currentPage}:header-title4`)}</h1>
      </div>
      <div className="middle__description">
      <p>{t(`${currentPage}:description4`)}</p>  
            </div>
    </div>
    <div className="middle__item">
      <div className="middle__title">
        <h1>{t(`${currentPage}:header-title5`)}</h1>
      </div>
      <div className="middle__description">
      <p>{t(`${currentPage}:description5`)}</p>  
            </div>
    </div>
  </div>
</div>

    </Layout>
  );
};

export default About;
