import React, { useState, useEffect } from "react";
import AdminMenu from "../../components/Layout/AdminMenu";
import Layout from "../../components/Layout/Layout";
import axios from "axios";
import toast from "react-hot-toast";
import { Link} from "react-router-dom";


const Users = () => {
  const [users, setUsers] = useState([]);

  // Get all users
  const getAllUsers = async () => {
    try {
      const { data } = await axios.get("/api/v1/users/get-users");
      setUsers(data.users);
    } catch (error) {
      console.log(error);
      toast.error("Something Went Wrong");
    }
  };

  // Lifecycle method
  useEffect(() => {
    getAllUsers();
  }, []);

  return (
    <Layout>
    <div className="container-fluid">
      <div className="row">
      <div className="col-md-3">
            <AdminMenu />
          </div>
      <div className="col-md-9">
        <h1 className="text-center">Utilizatori</h1>
        <table className="table">
          <thead>
            <tr>
              <th>Nume</th>
              <th>Email</th>
              <th>Telefon</th>
              <th>Adres</th>
              <th>Rspuns Securitate</th>
              <th>Rol</th>
              <th>Aciuni</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user._id}>
                <td>{user.name}</td>
                <td>{user.email}</td>
                <td>{user.phone}</td>
                <td>{user.address}</td>
                <td>{user.answer}</td>
                <td>{user.role === 0 ? "Utilizator" : "Admin"}</td>
                <td>
                  <Link to={`/dashboard/admin/user/${user._id}`}>Vezi Detalii</Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
    </div>
  </Layout>
  );
};

export default Users;
