import React, { useEffect, useState } from "react";
import { NavLink, Link } from "react-router-dom";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import { useCart } from "../../context/cart";
import { Badge } from "antd";
import { useTranslation } from 'react-i18next';
import { useNavigate } from "react-router-dom";
import i18n from 'i18next';
// Component separat pentru schimbarea limbii
const LanguageSwitcher = () => {
  const { i18n } = useTranslation();

  const changeLanguage = (language) => {
    if (['ro', 'ru'].includes(language)) {
      i18n.changeLanguage(language);
    }
  };
  const currentLanguage = i18n.language;
  return (
    <div>
      <button
        className={`langbtn ${currentLanguage === 'ro' ? 'active' : ''}`}
        onClick={() => changeLanguage('ro')}
      >
        RO
      </button>
      <button
        className={`langbtn ${currentLanguage === 'ru' ? 'active' : ''}`}
        onClick={() => changeLanguage('ru')}
      >
        RU
      </button>
    </div>
  );
};
const loadPageTranslations = (lang, page) => {
  return import(`../../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};
const Header = () => {
  const navigate = useNavigate();
  const [auth, setAuth] = useAuth();
  const [cart] = useCart();
  const { i18n } = useTranslation();
  const user = auth && auth.user;
  if (user) {
    // Acum poi accesa n siguran proprietile lui user
    const { email, name } = user;
  }
  const handleLogout = () => {
    setAuth(null);  // sau seteaz la un obiect iniial curat, depinde de implementarea useAuth
    localStorage.removeItem("auth");
    toast.success(i18n.t("Logout Successfully"));
    window.location.reload();  // Foreaz rencrcarea paginii pentru a cura complet starea    
    navigate('/');  // O bun practic este s redirecionezi utilizatorul la pagina de login sau home
  };
  useEffect(() => {
    if (!auth) {
      // Utilizatorul a fost deconectat, reseteaz orice stare specific utilizatorului
      // i poate redirecioneaz utilizatorul
      navigate('/');
    }
  }, [auth]);
  
  const { t } = useTranslation();
  const currentPage = 'Header';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);

  return (
    <nav className="navbar navbar-expand-lg bg-body-tertiary">
      <div className="container-fluid containerr elements">
        <Link to="/" className="navbar-brand">
          <img src="../../../images/logo.svg" className="imagelogo" alt="logo" />
        </Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink to="/saltele" className="nav-link">{t(`${currentPage}:saltele`)}</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/about" className="nav-link">{t(`${currentPage}:desprenoi`)}</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/contact" className="nav-link">{t(`${currentPage}:contacte`)}</NavLink>
            </li>
          </ul>
          <div className="right-nav">
            {!auth?.user ? (
              <>
                <NavLink to="/register" className="nav-link">{t(`${currentPage}:inregistrare`)}</NavLink>
                <NavLink to="/login" className="nav-link">{t(`${currentPage}:logare`)}</NavLink>
              </>
            ) : (
              <div className="dropdown">
                <button className="nav-link dropdown-toggle" data-bs-toggle="dropdown" style={{ border: "none", backgroundColor:"transparent" }}>{auth?.user?.name}</button>
                <ul className="dropdown-menu">
                  <li><NavLink to={`/dashboard/${auth?.user?.role === 1 ? "admin" : "user"}`} className="dropdown-item">{t(`${currentPage}:profil`)}</NavLink></li>
                  <li><button onClick={handleLogout} className="dropdown-item">{t(`${currentPage}:loguot`)}</button></li>
                </ul>
              </div>
            )}
            <LanguageSwitcher />
            <NavLink to="/cart" className="nav-link">
              <Badge count={cart?.length} showZero className="numarcart">
                <img src="../../../images/cart.svg" alt="cart" className="cartimage"/>
              </Badge>
            </NavLink>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Header;
