import React, { useState, useEffect } from "react";
import Layout from "./../components/Layout/Layout";
import { useNavigate, Link } from "react-router-dom";
import axios from "axios";
import { Checkbox, Radio, Spin } from "antd";
import { Weight } from "../components/Weight";
import { useTranslation } from 'react-i18next';
// import { useCart } from "../context/cart";

import i18n from 'i18next';
import toast from "react-hot-toast";

const Saltele = () => {
  const { i18n } = useTranslation();
  const navigate = useNavigate();
  // const [cart, setCart] = useCart();
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setRadio] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [showSpinner, setShowSpinner] = useState(false);

  const getCategoryName = (category) => {
    const language = i18n.language;
    return language === 'ro' ? category.nameRo : category.nameRu;
  };
  //get all cat
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/get-category");
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllCategory();
    getTotal();
    getAllProducts();
  }, []);

  //get products
  const getAllProducts = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`/api/v1/product/product-list/${page}`);
      setLoading(false);
      setProducts(data.products);
    } catch (error) {
      setLoading(false);
      console.log(error);
    }
  };
  useEffect(() => {
    if (!checked.length && !radio.length) getAllProducts();
  }, [checked, radio]);
  useEffect(() => {
    // Dac filtrele sunt resetate, preia toate produsele
    if (!checked.length && !radio.length) {
      getAllProducts();
    }
  }, [checked.length, radio.length]);

  useEffect(() => {
    // Dac sunt aplicate filtrele, filtreaz produsele
    if (checked.length || radio.length) {
      filterProduct();
    }
  }, [checked, radio]);

  //getTOtal COunt
  const getTotal = async () => {
    try {
      const { data } = await axios.get("/api/v1/product/product-count");
      setTotal(data?.total);
    } catch (error) {
      console.log(error);
    }
  };

  // useEffect(() => {
  //   localStorage.setItem("cart", JSON.stringify(cart));
  // }, [cart]);



  useEffect(() => {
    if (page === 1) return;
    loadMore();
  }, [page]);
  //load more
  const loadMore = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(`/api/v1/product/product-list/${page}`);
      setLoading(false);
      setProducts([...products, ...data?.products]);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  // filter by cat
  const handleFilter = (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    setChecked(all);
  };
  useEffect(() => {
    if (!checked.length || !radio.length) getAllProducts();
  }, [checked.length, radio.length]);

  useEffect(() => {
    if (checked.length || radio.length) filterProduct();
  }, [checked, radio]);

  //get filterd product
  const filterProduct = async () => {
    setShowSpinner(true);
    try {
      const { data } = await axios.post(`/api/v1/product/product-filters`, {
        checked,
        radio,
      });
      // console.log(data?.products); // Vezi ce produse sunt returnate de backend
      setProducts(data?.products);
    } catch (error) {
      console.log(error);
    } finally {
      setShowSpinner(false);
    }
  };


  const getValueByLang = (item, key) => {
    const language = i18n.language;
    return language === 'ro' ? item[`${key}_ro`] : item[`${key}_ru`];
  };
  const loadPageTranslations = (lang, page) => {
    return import(`../translations/${lang}/${page}.json`)
      .then(resources => {
        i18n.addResourceBundle(lang, page, resources.default);
        return true; // Indic faptul c ncrcarea a reuit
      })
      .catch(err => {
        console.error("Failed to load translations", err);
        return false; // Indic un eec n ncrcarea traducerilor
      });
  };
  const { t } = useTranslation();
  const currentPage = 'Saltele';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);

  return (
    <Layout title={"ALl Products - Best offers "}>
      {showSpinner && (
        <div style={{
          position: 'fixed', // Fixeaz div-ul peste tot coninutul
          top: 0,
          left: 0,
          width: '100%', // Asigur c acoper ntreaga lime
          height: '100%', // Asigur c acoper ntreaga nlime
          backgroundColor: 'rgba(0, 0, 0, 0.3)', // Fundal semi-transparent
          display: 'flex',
          justifyContent: 'center', // Centreaz orizontal spinnerul
          alignItems: 'center', // Centreaz vertical spinnerul
          zIndex: 1000, // Asigur-te c se afl peste alte elemente

        }}>
          <Spin size="large" />
        </div>
      )}
      <div className="saltele-page">
        <div className="filter">
          <div className="filter__sidebar">
            <h4 className="filter__heading">{t(`${currentPage}:categorie`)}</h4>
            <div className="filter__categories">
              {categories?.map((category) => (
                <Checkbox
                  key={category._id}
                  onChange={(e) => handleFilter(e.target.checked, category._id)}
                  className="filter__checkbox"
                >
                  {getCategoryName(category)}
                </Checkbox>
              ))}

            </div>
            <h4 className="filter__heading filter__heading-weight">{t(`${currentPage}:greutate`)}</h4>
            <div className="filter__price-filters">
              <Radio.Group onChange={(e) => setRadio(e.target.value)} className="filter-sidebar__radio-group">
                {Weight?.map((p) => (
                  <div key={p._id} className="filter__price-filter">
                    <Radio value={p.array} className="filter__checkbox">{p.name} {t(`${currentPage}:kg`)}</Radio>
                  </div>
                ))}

              </Radio.Group>
            </div>
            <div className="filter__actions">
              <button
                className="filter__button"
                onClick={() => window.location.reload()}
              >
               {t(`${currentPage}:reset`)}
              </button>
            </div>
          </div>
        </div>
        <div className="products__container">

          {!showSpinner && (
            <div className="product__related-list  allproducts">
              {products.map((relatedProduct) => (
                <div className="product__card " key={relatedProduct._id}>
                  <Link to={`/product/${relatedProduct.slug}`} className="product__card-link" rel="noopener noreferrer">
                    <img
                      src={`/api/v1/product/product-photo/${relatedProduct.slug}`}
                      className="product__card-image card-img-top"
                      alt={relatedProduct.name}
                    />
                  </Link>
                  <div className="product__card-body ">
                    <Link to={`/product/${relatedProduct.slug}`} className="product__card-title card-title" rel="noopener noreferrer">
                      {getValueByLang(relatedProduct, 'name')}
                    </Link>
                    <ul className="product__card-features">
                      {relatedProduct.characteristics.slice(0, 4).map((char, index) => (
                        <li className="product__card-feature" key={`${relatedProduct._id}-${index}`}>
                          {getValueByLang(char, 'key')}: {getValueByLang(char, 'value')}
                        </li>
                      ))}

                    </ul>
                    <p className="product__card-price card-text">
                    {t(`${currentPage}:dela`)}  {relatedProduct.sizes && relatedProduct.sizes.length > 0 ? relatedProduct.sizes[0].price : "N/A"} {t(`${currentPage}:lei`)}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          )}
          {/* {products && products.length < total && (
            <div className="products__more">
              <button
                className="products__more-button button"
                onClick={(e) => {
                  e.preventDefault();
                  setPage(page + 1);
                }}
              >
                {loading ? "Loading ..." : "Load more"}
              </button>
            </div>
          )} */}
        </div>
      </div>
    </Layout>
  );
};

export default Saltele;