
import Layout from "./../components/Layout/Layout";
import React, { useEffect, useState } from "react";
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};
const Delivery = () => {
  const { t } = useTranslation();
  const currentPage = 'Delivery';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true); 
      }
    });
  }, []);
  return (
    <Layout title={t(`${currentPage}:title`)}>
     <div className="deliveryy">
  <div className="image">
    <img src="../images/delivery.png" alt=""/>
  </div>
  <div className="delivery container">
    <div className="delivery__inner">
      <div className="delivery__title">
        <h1>{t(`${currentPage}:options`)}</h1>
      </div>
      <div className="delivery__description">
        <ol>
          <li className="delivery__description-title">{t(`${currentPage}:options-title1`)}</li>
          <ul className>
            <li className="delivery__description-title-desc"><b>{t(`${currentPage}:description-title-first`)}</b> {t(`${currentPage}:description-title-last`)}</li>
            <li className="delivery__description-title-desc"><b>{t(`${currentPage}:description-title-first1`)}</b> {t(`${currentPage}:description-title-last1`)} </li>
          </ul>
          <li className="delivery__description-title">{t(`${currentPage}:options-title2`)}</li>
          <ul className>
            <li className="delivery__description-title-desc"><b>{t(`${currentPage}:description-title-first2`)}</b> {t(`${currentPage}:description-title-last2`)}</li>
          </ul>
        </ol>
      </div>
    </div>
  </div>
</div>


    </Layout>
  );
};

export default Delivery;
