import React, { useState, useEffect } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "./../../components/Layout/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { Select, Button, Input } from "antd";
import { useNavigate, useParams } from "react-router-dom";
const { Option } = Select;

const UpdateProduct = () => {
  const [id, setId] = useState("");
  const params = useParams();
  const navigate = useNavigate();
  const [kg, setKg] = useState("");
  const [categories, setCategories] = useState([]);
  const [nameRo, setNameRo] = useState("");
  const [nameRu, setNameRu] = useState("");
  const [descriptionRo, setDescriptionRo] = useState("");
  const [descriptionRu, setDescriptionRu] = useState("");
  const [category, setCategory] = useState("");
  const [sizes, setSizes] = useState([{ size: "", price: "" }]);
  const [important, setImportant] = useState([{ importantspec: "" }]);
  const [characteristics, setCharacteristics] = useState([{ key: "", value: "" }]);
  const [technicalStructure, setTechnicalStructure] = useState([{ technical_structurespec: "" }]);
  const [photo, setPhoto] = useState(null);

  const getSingleProduct = async () => {
    try {
      const { data } = await axios.get(`/api/v1/product/get-product/${params.slug}`);
      if (data && data.product) {
        // Setarea strilor pe baza datelor produsului
        setNameRo(data.product.name_ro || "");
        setNameRu(data.product.name_ru || "");
        setId(data.product._id);
        setDescriptionRo(data.product.description_ro || "");
        setDescriptionRu(data.product.description_ru || "");
        setKg(data.product.kg || "");
        setCategory({
          _id: data.product.category?._id,
          name_ro: data.product.category?.nameRo,
          name_ru: data.product.category?.nameRu,
        });
        setSizes(data.product.sizes || [{ size: "", price: "" }]);
        setImportant(data.product.important || [{ importantspec: "" }]);
        setCharacteristics(data.product.characteristics || [{ key: "", value: "" }]);
        setTechnicalStructure(data.product.technical_structure || [{ technical_structurespec: "" }]);
        // setPhoto(data.product.photo || null); // Presupunnd c poi gestiona URL-ul unei imagini sau un obiect File
        if (data.product.photo && data.product.photo.data) {
          const imageBase64 = Buffer.from(data.product.photo.data).toString('base64');
          const imageSrc = `data:${data.product.photo.contentType};base64,${imageBase64}`;
          setPhoto(imageSrc);
        } else {
          setPhoto(null);
        }
      }
    } catch (error) {
      console.error("Failed to fetch product details:", error);
      toast.error("Failed to fetch product details.");
    }
  };

  useEffect(() => {
    getSingleProduct();
    // A doua linie de mai jos pentru a elimina avertismentul de dependen lips din eslint
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [params.slug]); // Adaug `params.slug_ro` ca dependen pentru a rencrca produsul cnd se schimb slug-ul

  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/get-category");
      // console.log("Data fetched from API:", data); // Log pentru a vedea datele
      if (data?.success) {
        setCategories(data.category); // Folosete 'data.category', nu 'data.categories'

      } else {
        toast.error("Failed to fetch categories. Please check the API or network!");
        setCategories([]);
      }
    } catch (error) {
      console.error("Error fetching categories:", error);
      toast.error("An error occurred while fetching categories");
      setCategories([]);
    }
  };
  useEffect(() => {
    getAllCategories();
    getSingleProduct();
  }, []);

  useEffect(() => {
    if (categories.length > 0) {
      // Actualizeaz categoria produsului dac categoriile sunt disponibile i produsul a fost ncrcat
      const currentCategory = categories.find(c => c._id === category._id);
      if (currentCategory) {
        setCategory(currentCategory);
      }
    }
  }, [categories]);

  useEffect(() => {
    if (categories.length > 0 && category._id) {
      // Asigurm c categoria selectat este nc valabil n lista ncrcat
      const matchingCategory = categories.find(c => c._id === category._id);
      if (matchingCategory) {
        setCategory(matchingCategory);
      } else {
        // Dac nu gsim categoria, setm prima categorie disponibil ca fallback
        setCategory(categories[0]);
      }
    }
  }, [category._id, categories]);

  const handlePhotoChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setPhoto(file);
    } else {
      setPhoto(null);
    }
  };
  //create product function
  const handleUpdate = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("name_ro", nameRo);
    formData.append("name_ru", nameRu);
    formData.append("description_ro", descriptionRo);
    formData.append("description_ru", descriptionRu);
    formData.append("kg", kg);
    formData.append("category", category._id);
    formData.append("sizes", JSON.stringify(sizes));
    formData.append("important", JSON.stringify(important));
    formData.append("characteristics", JSON.stringify(characteristics));
    formData.append("technical_structure", JSON.stringify(technicalStructure));
    if (photo) {
      formData.append("photo", photo);
    }

    try {
      const response = await axios.put(`/api/v1/product/update-product/${params.slug}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });


      if (response.data.success) {
        toast.success("Produsul a fost actualizat cu succes.");
        navigate("/dashboard/admin/products");
      } else {
        toast.error(response.data.message || "Actualizarea produsului nu a reuit.");
      }
    } catch (error) {
      toast.error(error.response?.data?.message || "A aprut o eroare n timpul actualizrii.");
    }
  };





  //delete a product
  const handleDelete = async () => {
    const confirmed = window.confirm("Are you sure you want to delete this product?");
    if (!confirmed) return;

    try {
      const { data } = await axios.delete(`/api/v1/product/delete-product/${id}`);
      if (data.success) {
        toast.success("Product deleted successfully");
        navigate("/dashboard/admin/products");
      } else {
        toast.error(data.message || "Failed to delete the product");
      }
    } catch (error) {
      console.error("Error deleting product:", error);
      toast.error("Something went wrong");
    }
  };



  const handleSizeChange = (index, key, value) => {
    const updatedSizes = [...sizes];
    updatedSizes[index][key] = value;
    setSizes(updatedSizes);
  };

  const handleAddSize = () => {
    setSizes([...sizes, { size: "", price: "" }]);
  };

  const handleRemoveSize = (index) => {
    const updatedSizes = [...sizes];
    updatedSizes.splice(index, 1);
    setSizes(updatedSizes);
  };

  const handleCharacteristicChange = (index, key, value) => {
    const updatedCharacteristics = [...characteristics];
    updatedCharacteristics[index][key] = value;
    setCharacteristics(updatedCharacteristics);
  };
  const handleCategoryChange = (value) => {
    // Gsete categoria selectat utiliznd ID-ul ales din Select.
    const selectedCategory = categories.find(c => c._id === value);
    console.log("Categorie selectat:", selectedCategory);
    // Logheaz categoria selectat pentru debug.


    // Seteaz categoria selectat n stare sau un obiect gol dac nu se gsete nimic.
    setCategory(selectedCategory || {});
  };
  useEffect(() => {
    if (categories.length > 0 && category?._id) {
      // Gsete categoria din lista ncrcat bazat pe `_id` al categoriei produsului
      const currentCategory = categories.find(c => c._id === category._id) || categories[0];
      setCategory(currentCategory);
    }
  }, [categories]); // Dependena aici este doar `categories` pentru a reactualiza cnd lista de categorii se schimb.

  const handleAddCharacteristic = () => {
    setCharacteristics([...characteristics, { key: "", value: "" }]);
  };

  const handleRemoveCharacteristic = (index) => {
    const updatedCharacteristics = [...characteristics];
    updatedCharacteristics.splice(index, 1);
    setCharacteristics(updatedCharacteristics);
  };

  const handleImportantChange = (index, key, value) => {
    const updatedImportants = [...important];
    updatedImportants[index][key] = value;
    setImportant(updatedImportants);
  };

  const handleAddImportant = () => {
    setImportant([...important, { importantspec: "" }]);
  };

  const handleRemoveImportant = (index) => {
    const updatedImportants = [...important];
    updatedImportants.splice(index, 1);
    setImportant(updatedImportants);
  };

  const handleTechnicalStructureChange = (index, key, value) => {
    const updatedTechnicalStructures = [...technicalStructure];
    updatedTechnicalStructures[index][key] = value;
    setTechnicalStructure(updatedTechnicalStructures);
  };

  const handleAddTechnicalStructure = () => {
    setTechnicalStructure([...technicalStructure, { technical_structurespec: "" }]);
  };

  const handleRemoveTechnicalStructure = (index) => {
    const updatedTechnicalStructures = [...technicalStructure];
    updatedTechnicalStructures.splice(index, 1);
    setTechnicalStructure(updatedTechnicalStructures);
  };
  return (
    <Layout title={"Dashboard - Update Product"}>
      <div className="container-fluid m-3 p-3">
        <div className="row">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9">
            <h1>Update Product</h1>
            <form onSubmit={handleUpdate}>
              <div className="mb-3">
                <label>Name (RO)</label>
                <Input value={nameRo} onChange={(e) => setNameRo(e.target.value)} />
              </div>
              <div className="mb-3">
                <label>Name (RU)</label>
                <Input value={nameRu} onChange={(e) => setNameRu(e.target.value)} />
              </div>
              <div className="mb-3">
                <label>Description (RO)</label>
                <Input.TextArea value={descriptionRo} onChange={(e) => setDescriptionRo(e.target.value)} />
              </div>
              <div className="mb-3">
                <label>Description (RU)</label>
                <Input.TextArea value={descriptionRu} onChange={(e) => setDescriptionRu(e.target.value)} />
              </div>
              <div className="mb-3">
                <label>Weight (kg)</label>
                <Input value={kg} onChange={(e) => setKg(e.target.value)} type="number" />
              </div>
              <div className="mb-3">

                <select value={category._id} onChange={handleCategoryChange} style={{ width: '100%', marginBottom: '20px' }}>

                  {categories.map(c => (
                    <option key={c._id} value={c._id}>{c.nameRo}</option> // Folosete 'c.nameRo' pentru a afia numele categoriei n romn
                  ))}
                </select>

              </div>

              {/* Sizes */}
              {sizes.map((size, index) => (
                <div key={index} className="mb-3">
                  <label>Size</label>
                  <Input value={size.size} onChange={(e) => handleSizeChange(index, 'size', e.target.value)} />
                  <label>Price</label>
                  <Input value={size.price} onChange={(e) => handleSizeChange(index, 'price', e.target.value)} type="number" />
                  <Button onClick={() => handleRemoveSize(index)}>Remove Size</Button>
                </div>
              ))}
              <Button onClick={handleAddSize}>Add Size</Button>

              {/* Important */}
              {important.map((imp, index) => (
                <div key={index} className="mb-3">
                  <label>Important Spec (RO)</label>
                  <Input value={imp.importantspec_ro} onChange={(e) => handleImportantChange(index, 'importantspec_ro', e.target.value)} />
                  <label>Important Spec (RU)</label>
                  <Input value={imp.importantspec_ru} onChange={(e) => handleImportantChange(index, 'importantspec_ru', e.target.value)} />
                  <Button onClick={() => handleRemoveImportant(index)}>Remove Important Spec</Button>
                </div>
              ))}
              <Button onClick={handleAddImportant}>Add Important Spec</Button>

              {/* Characteristics */}
              {characteristics.map((char, index) => (
                <div key={index} className="mb-3">
                  <label>Characteristic Key (RO)</label>
                  <Input value={char.key_ro} onChange={(e) => handleCharacteristicChange(index, 'key_ro', e.target.value)} />
                  <label>Characteristic Value (RO)</label>
                  <Input value={char.value_ro} onChange={(e) => handleCharacteristicChange(index, 'value_ro', e.target.value)} />
                  <label>Characteristic Key (RU)</label>
                  <Input value={char.key_ru} onChange={(e) => handleCharacteristicChange(index, 'key_ru', e.target.value)} />
                  <label>Characteristic Value (RU)</label>
                  <Input value={char.value_ru} onChange={(e) => handleCharacteristicChange(index, 'value_ru', e.target.value)} />
                  <Button onClick={() => handleRemoveCharacteristic(index)}>Remove Characteristic</Button>
                </div>
              ))}
              <Button onClick={handleAddCharacteristic}>Add Characteristic</Button>

              {/* Technical Structure */}
              {technicalStructure.map((tech, index) => (
                <div key={index} className="mb-3">
                  <label>Technical Structure Spec (RO)</label>
                  <Input value={tech.technical_structurespec_ro} onChange={(e) => handleTechnicalStructureChange(index, 'technical_structurespec_ro', e.target.value)} />
                  <label>Technical Structure Spec (RU)</label>
                  <Input value={tech.technical_structurespec_ru} onChange={(e) => handleTechnicalStructureChange(index, 'technical_structurespec_ru', e.target.value)} />
                  <Button onClick={() => handleRemoveTechnicalStructure(index)}>Remove Technical Structure</Button>
                </div>
              ))}
              <Button onClick={handleAddTechnicalStructure}>Add Technical Structure</Button>

              {/* Photo */}
              <div className="mb-3">
                {photo ? (
                  <div className="text-center">
                    <img
                      src={URL.createObjectURL(photo)}
                      alt="product_photo"
                      height={"200px"}
                      className="img img-responsive"
                    />
                  </div>
                ) : (
                  <div className="text-center">
                    <img
                      src={`/api/v1/product/product-photo/${params.slug}`}
                      alt="product_photo"
                      height={"200px"}
                      className="img img-responsive"
                    />

                  </div>
                )}
              </div>


              <Button type="submit" onClick={handleUpdate} >Update Product</Button>
            </form>
            <Button onClick={handleDelete} danger>Delete Product</Button>
          </div>
        </div>
      </div>

    </Layout>
  );
};
export default UpdateProduct;