
import React,{useEffect, useState }  from 'react';
import Layout from '../../components/Layout/Layout';
import  toast  from 'react-hot-toast';
import axios from 'axios';
import {useNavigate} from 'react-router-dom';
import { useAuth } from '../../context/auth';
import { useTranslation } from 'react-i18next';
import i18n from 'i18next';
const loadPageTranslations = (lang, page) => {
    return import(`../../translations/${lang}/${page}.json`)
      .then(resources => {
        i18n.addResourceBundle(lang, page, resources.default);
        return true; // Indic faptul c ncrcarea a reuit
      })
      .catch(err => {
        console.error("Failed to load translations", err);
        return false; // Indic un eec n ncrcarea traducerilor
      });
  };
const Login = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [auth,setAuth] = useAuth()
    const navigate= useNavigate();
    const location = useNavigate();
    const { t } = useTranslation();
    const currentPage = 'Login';
    const [translationsLoaded, setTranslationsLoaded] = useState(false);
  
    useEffect(() => {
      const currentLang = i18n.language;
      loadPageTranslations(currentLang, currentPage).then((loaded) => {
        if (loaded) {
          setTranslationsLoaded(true); 
        }
      });
    }, []);
    // form function
    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const res = await axios.post(`/api/v1/auth/login`, { email, password });
        if(res && res.data.success){
            toast.success(res.data && res.data.message);
            setAuth({
                ...auth,
                user: res.data.user,
                token: res.data.token,
            }); 
            localStorage.setItem('auth',JSON.stringify(res.data));
            navigate(location.state || "/");
        }else{
            toast.error(res.data.message);
        }
        }

        catch (error) {
            console.log(error);
            toast.error("Somethinf went wrong");
        }
    };
    return (
        <Layout title='Login'>
            <div className="login-container">
                <div className="login-form">
                    <h2 className="login-form__title">{t(`${currentPage}:conn`)}  i-sleep</h2>
                    <form onSubmit={handleSubmit}>
                        <div className="login-form__group">
                            <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required placeholder={t(`${currentPage}:email`)} />
                        </div>
                        <div className="login-form__group">
                            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required placeholder={t(`${currentPage}:pass`)} />
                        </div>
                        <div className="login-form__forgot-pass" onClick={()=>{navigate('/forgot-password')}}>
                        {t(`${currentPage}:forg`)}
                        </div>
                        <button type="submit" className="login-form__submit">{t(`${currentPage}:conect`)} </button>
                    </form>
                </div>
            </div>
        </Layout>

  )
}

export default Login