import React, { useState, useEffect } from "react";
import { useTranslation } from 'react-i18next';
import Layout from "./../components/Layout/Layout";
import i18n from 'i18next';

const loadPageTranslations = (lang, page) => {
  return import(`../translations/${lang}/${page}.json`)
    .then(resources => {
      i18n.addResourceBundle(lang, page, resources.default);
      return true; // Indic faptul c ncrcarea a reuit
    })
    .catch(err => {
      console.error("Failed to load translations", err);
      return false; // Indic un eec n ncrcarea traducerilor
    });
};

const Warranty = () => {
  const { t } = useTranslation();
  const currentPage = 'Warranty';
  const [translationsLoaded, setTranslationsLoaded] = useState(false);

  useEffect(() => {
    const currentLang = i18n.language;
    loadPageTranslations(currentLang, currentPage).then((loaded) => {
      if (loaded) {
        setTranslationsLoaded(true);
      }
    });
  }, []);


  return (
    <Layout title={t(`${currentPage}:meta.title`)}>
      <div className="warranty container">
        
        <div className="warranty__top-section">
          
          <section className="warranty__customer-rights">
          <h1 className="warranty__title">{t(`${currentPage}:header`)}</h1>
        <p className="warranty__introduction">{t(`${currentPage}:introduction`)}</p>
            <h2 className="warranty__customer-rights-header">{t(`${currentPage}:customerRights:header`)}</h2>
            <ul className="warranty__list">
              <li className="warranty__list-item">{t(`${currentPage}:customerRights:returnOrReplace`)}</li>
              <li className="warranty__list-item">{t(`${currentPage}:customerRights:supportService`)}</li>
            </ul>
            <p className="warranty__commitment">{t(`${currentPage}:commitment`)}</p>
            <p className="warranty__important-notice last">{t(`${currentPage}:importantNotice`)}</p>
          </section>
          <section className="warranty__customer-left">
            <img src="../images/garantiefata.png" alt="warranty" className="warrantyimage"/>
          </section>

        </div>


        <section className="warranty__manufacturer-guarantee">
          <h2 className="warranty__manufacturer-guarantee-header">{t(`${currentPage}:manufacturerGuarantee:header`)}</h2>
          <p className="warranty__manufacturer-guarantee-life-span">{t(`${currentPage}:manufacturerGuarantee:lifeSpan`)}</p>
          <p className="warranty__manufacturer-guarantee-compliance">{t(`${currentPage}:manufacturerGuarantee:compliance`)}</p>
          <p className="warranty__manufacturer-guarantee-warranty-service">{t(`${currentPage}:manufacturerGuarantee:warrantyService`)}</p>
          <p className="warranty__manufacturer-guarantee-replacement">{t(`${currentPage}:manufacturerGuarantee"replacement`)}</p>
        </section>

        <section className="warranty__exclusions">
          <h2 className="warranty__exclusions-header">{t(`${currentPage}:exclusions:header`)}</h2>
          <ul className="warranty__list">
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:liquidSpill`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:sharpObjects`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:moldAndOdor`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:improperSurface`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:incompatibleFrame`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:exclusions:exceededWeight`)}</li>
          </ul>
          <p className="warranty__exclusions-notification">{t(`${currentPage}:exclusions:notification`)}</p>
          <p className="warranty__exclusions-address">{t(`${currentPage}:exclusions:address`)}</p>
        </section>

        <section className="warranty__mattress-use">
          <h2 className="warranty__mattress-use-header">{t(`${currentPage}:mattressCare:header`)}</h2>
          <ul className="warranty__list">
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:packagingRemoval`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:frameRecommendation`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:indoorUsage`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:positionChange`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:ventilation`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:moistEnvironments`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:cleaningWarning`)}</li>
            <li className="warranty__list-item">{t(`${currentPage}:mattressCare:protectiveCover`)}</li>
          </ul>
        </section>

        <p className="warranty__i-sleep-statement">{t(`${currentPage}:iSleepStatement`)}</p>
      </div>
    </Layout>
  );
};

export default Warranty;
